import * as d3 from "d3";
// import "@fortawesome/fontawesome-free/css/all.min.css";
import styles from "./forceGraph.module.css";

export function runForceGraph(
  container,
  linksData,
  nodesData,
  funcColor,
  nodeHoverTooltip
) {
  const ids = nodesData.map((a) => a.id);
  const filteredLinks = (l, myIDS) => {
    let k = l.filter(
      (d) => myIDS.includes(d.source) && myIDS.includes(d.target)
    );
    return k;
  };

  const nodes = nodesData.map((d) => Object.assign({}, d));
  const links = filteredLinks(linksData, ids).map((d) => Object.assign({}, d));

  const containerRect = container.getBoundingClientRect();
  const height = containerRect.height;
  const width = containerRect.width;

  const node_radius = 25;
  const font_size = "11px";

  const node_highlighted = "black";
  const node_not_highlighted = "white";

  const link_highlighted = "black";
  const link_not_highlighted = "#ccc";
  const arrowPath = "M 4 -1.5 L 7 0 L 4 1.5";

  //   ============================================
  const linkColor = (d) => {
    return d.mandatory ? "#F00" : "#0F0";
  };

  const icon = (d) => {
    return d.code; // d.gender === "male" ? "X" : "Y";
  };

  const getClass = (d) => {
    return styles.male; //  d.gender === "male" ? styles.male : styles.female;
  };

  const drag = (simulation) => {
    const dragstarted = (d) => {
      if (!d3.event.active) simulation.alphaTarget(0.3).restart();
      d.fx = d.x;
      d.fy = d.y;
    };

    const dragged = (d) => {
      d.fx = d3.event.x;
      d.fy = d3.event.y;
    };

    const dragended = (d) => {
      if (!d3.event.active) simulation.alphaTarget(0);
      d.fx = null;
      d.fy = null;
    };

    return d3
      .drag()
      .on("start", dragstarted)
      .on("drag", dragged)
      .on("end", dragended);
  };

  //   ============================================

  // Add the tooltip element to the graph
  const tooltip = document.querySelector("#graph-tooltip");
  if (!tooltip) {
    const tooltipDiv = document.createElement("div");
    tooltipDiv.classList.add(styles.tooltip);
    tooltipDiv.style.opacity = "0";
    tooltipDiv.id = "graph-tooltip";
    document.body.appendChild(tooltipDiv);
  }
  const div = d3.select("#graph-tooltip");

  const addTooltip = (hoverTooltip, d, x, y) => {
    const sw = document.body.querySelector(".App").offsetWidth;
    const sh = document.body.querySelector(".App").offsetHeight;
    const rw = document.body.querySelector(".right").offsetWidth;
    const rh = document.body.querySelector(".right").offsetHeight;

    const tw = div.width;

    div
      .transition()
      .duration(200)
      .style("opacity", 0.9);
    div
      .html(hoverTooltip(d))
	  .style("width", "160px")
      .style("left", `${sw - rw + 10}px`)
      .style("top", `${y - 28}px`);
  };

  const removeTooltip = () => {
    div
      .transition()
      .duration(200)
      .style("opacity", 0);
  };

  //   ============================================

  // console.log(width, height)

  const simulation = d3
    .forceSimulation(nodes)
    .force(
      "link",
      d3
        .forceLink(links)
        .distance(100)
        .id((d) => d.id)
    )
    .force("charge", d3.forceManyBody().strength(-300))
    .force("x", d3.forceX(width / 2))
    .force("y", d3.forceY(height / 2))
    .force("collide", d3.forceCollide(node_radius + 5));

  const svg = d3
  .select(container)
  .append("svg");
  
	// arrow
  svg
    .append("marker")
    .attr("id", "arrowhead-not-highlighted")
    .attr("viewBox", "-0 -5 10 10")
    .attr("refX", 13)
    .attr("refY", 0)
    .attr("orient", "auto")
    .attr("markerWidth", 13)
    .attr("markerHeight", 13)
    .append("svg:path")
    .attr("d", arrowPath)
    .attr("xoverflow", "visible")
    .attr("fill", link_not_highlighted);
	
	// arrow
	svg
    .append("marker")
    .attr("id", "arrowhead-highlighted")
    .attr("viewBox", "-0 -5 10 10")
    .attr("refX", 13)
    .attr("refY", 0)
    .attr("orient", "auto")
    .attr("markerWidth", 13)
    .attr("markerHeight", 13)
    .append("svg:path")
    .attr("d", arrowPath)
    .attr("xoverflow", "visible")
    .attr("fill", link_highlighted);
	

  const bg = svg
    .append("rect")
    .attr("width", width)
    .attr("height", height)
    .attr("fill", "white")
    .style("cursor", "default")
    .lower()
    .on("click", function(d) {
      exit_highlight(d);
    });

  const link = svg
    .append("g")
    .selectAll("line")
    .data(links)
    .enter()
    .append("line")
    // .attr("opacity", link_not_highlighted)
    .attr("marker-end", "url(#arrowhead-not-highlighted)")
    .attr("stroke", link_not_highlighted)
    .attr("stroke-dasharray", (d) => (d.mandatory ? 0 : 5))
    .attr("stroke-width", 3);

  const node = svg
    .append("g")
    .selectAll("circle")
    .data(nodes)
    .join("circle")
    .attr("r", node_radius)
    .attr("fill", (d) => funcColor(d.code))
    .attr("cx", width / 2)
    .attr("cy", height / 2)
    .attr("stroke", node_not_highlighted)
    .attr("stroke-width", 2)
    .style("cursor", "pointer")
    .call(drag(simulation));

  const label = svg
    .style("font-size", font_size)
    .append("g")
    .attr("class", "labels")
    .selectAll("text")
    .data(nodes)
    .enter()
    .append("text")
    .attr("text-anchor", "middle")
    .attr("dominant-baseline", "central")
    .attr("class", (d) => `fa ${getClass(d)}`)
    .text((d) => {
      return icon(d);
    })
    .style("fill", "black")
    .style("cursor", "pointer")
    .call(drag(simulation));

  const updateNodes = () => {
    svg.remove();

    //   console.log("i updated!!!", node, nodesData);
  };

  //   ============================================

  node
    .on("dbclick", (d) => {
		
	})
    .on("mouseover", (d) => {
      set_highlight(d);
      addTooltip(nodeHoverTooltip, d, d3.event.pageX + 15, d3.event.pageY);
    })
    .on("mouseout", (d) => {
      exit_highlight(d);
      removeTooltip();
    });

  function set_highlight(a) {
    const links_related = linksData.filter(
      (d) => d.source === a.id || d.target === a.id
    );
    const ids_temp = links_related
      .map((d) => (a.id === d.source ? d.target : d.source))
      .filter((id) => id !== a.id);

    // console.log(links_related, ids_temp)

    // node.style("stroke", (n) => {
    //   return (n.id === a.id) ? "red" : node_not_highlighted;
    // });

    node.style("stroke", (n) => {
      const pred1 = ids_temp.includes(n.id) || n.id === a.id;
      const pred2 = n.id === a.id;

      // highlight all linked nodes, but not a
      // return pred2 ? node_not_highlighted : (pred1 ? node_highlighted : node_not_highlighted)

      // hightlight only a
      return pred2 ? node_highlighted : node_not_highlighted;
    });

    link.style("stroke", (l) => {
      return a.id === l.source.id || a.id === l.target.id
        ? link_highlighted
        : link_not_highlighted;
    });
	link.style("marker-end", (l) => {
      return a.id === l.source.id || a.id === l.target.id
        ? "url(#arrowhead-highlighted)"
        : "url(#arrowhead-not-highlighted)";
    });
	
  }

  function exit_highlight(d) {
    svg.style("cursor", "move");

    node.style("stroke", node_not_highlighted);
    link.style("stroke", link_not_highlighted);
	link.style("marker-end", "url(#arrowhead-not-highlighted)");
  }

  function updatePos(pos, lim) {
    return Math.max(node_radius, Math.min(lim - node_radius, pos));
  }

  simulation.on("tick", () => {
    //update link positions
    link
      .attr("x1", (d) => {
        return (d.source.x = updatePos(d.source.x, width));
      })
      .attr("y1", (d) => {
        return (d.source.y = updatePos(d.source.y, height));
      })
      .attr("x2", (d) => {
        return (d.target.x = updatePos(d.target.x, width));
      })
      .attr("y2", (d) => {
        return (d.target.y = updatePos(d.target.y, height));
      });

    // .attr("x1", (d) => d.source.x)
    // .attr("y1", (d) => d.source.y)
    // .attr("x2", (d) => d.target.x)
    // .attr("y2", (d) => d.target.y);

    // update node positions
    node
      //   .attr("cx", (d) => d.x)
      //   .attr("cy", (d) => d.y);
      .attr("cx", (d) => {
        return (d.x = updatePos(d.x, width));
      })
      .attr("cy", (d) => {
        return (d.y = updatePos(d.y, height));
      });

    // update label positions
    label.attr("x", (d) => d.x).attr("y", (d) => d.y);
  });

  //   ============================================

  return {
    destroy: () => {
      simulation.stop();
    },
    update: () => {
      updateNodes();
    },
    nodes: () => {
      return svg.node();
    },
  };
}
